.absolute{
  position: absolute;
}

.relative{
  position: relative;
}

.col-span-12{
  grid-column: span 12 / span 12;
}

.col-span-10{
  grid-column: span 10 / span 10;
}

.col-start-1{
  grid-column-start: 1;
}

.col-start-2{
  grid-column-start: 2;
}

.mt-8{
  margin-top: 2rem;
}

.mb-16{
  margin-bottom: 4rem;
}

.mt-auto{
  margin-top: auto;
}

.mb-4{
  margin-bottom: 1rem;
}

.flex{
  display: flex;
}

.grid{
  display: grid;
}

.hidden{
  display: none;
}

.h-screen{
  height: 100vh;
}

.h-full{
  height: 100%;
}

.w-full{
  width: 100%;
}

.list-none{
  list-style-type: none;
}

.grid-cols-12{
  grid-template-columns: repeat(12, minmax(0, 1fr));
}

.flex-col{
  flex-direction: column;
}

.justify-end{
  justify-content: flex-end;
}

.justify-between{
  justify-content: space-between;
}

.gap-8{
  gap: 2rem;
}

.place-self-center{
  place-self: center;
}

.overflow-hidden{
  overflow: hidden;
}

.pt-16{
  padding-top: 4rem;
}

.text-center{
  text-align: center;
}

.text-6xl{
  font-size: 3.75rem;
  line-height: 1;
}

.text-xl{
  font-size: 1.25rem;
  line-height: 1.75rem;
}

.font-bold{
  font-weight: 700;
}

.text-green{
  --tw-text-opacity: 1;
  color: rgb(132 255 144 / var(--tw-text-opacity));
}

.text-blue{
  --tw-text-opacity: 1;
  color: rgb(163 233 255 / var(--tw-text-opacity));
}

.text-purple{
  --tw-text-opacity: 1;
  color: rgb(183 158 255 / var(--tw-text-opacity));
}

.text-red{
  --tw-text-opacity: 1;
  color: rgb(255 177 177 / var(--tw-text-opacity));
}

body {
  background: rgb(143, 100, 105);
  background: linear-gradient(142deg, rgb(184, 135, 140) 0%, rgb(132, 89, 160) 29%, rgb(106, 117, 192) 54%, rgb(118, 180, 196) 100%);
  color: white;
  font-family: "Roboto Mono", monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.material-symbols-outlined {
  font-variation-settings: "FILL" 1, "wght" 500, "GRAD" 200, "opsz" 48;
}

.circle {
  margin: 1rem 0;
}

.circle a {
  border: 2px solid whitesmoke;
  transition: all 0.2s ease;
  box-shadow: 0 0 0 0 #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  border-radius: 50%;
}

.circle a:hover {
  box-shadow: -6px 6px 0 0 #fff;
  -webkit-transform: translate(6px, -6px);
          transform: translate(6px, -6px);
}

.circle img {
  width: 20px;
  -webkit-filter: invert(1);
          filter: invert(1);
}

.main-btn {
  border: 2px solid whitesmoke;
  transition: all 0.2s ease;
  box-shadow: 0 0 0 0 #fff;
  padding: 0.75rem 2.5rem;
  border-radius: 10px;
}

.main-btn:hover {
  box-shadow: -6px 6px 0 0 #fff;
  -webkit-transform: translate(6px, -6px);
          transform: translate(6px, -6px);
}

.BackgroundObject {
  z-index: -999;
}

.hover\:text-green:hover{
  --tw-text-opacity: 1;
  color: rgb(132 255 144 / var(--tw-text-opacity));
}

.hover\:text-blue:hover{
  --tw-text-opacity: 1;
  color: rgb(163 233 255 / var(--tw-text-opacity));
}

.hover\:text-purple:hover{
  --tw-text-opacity: 1;
  color: rgb(183 158 255 / var(--tw-text-opacity));
}

.hover\:text-red:hover{
  --tw-text-opacity: 1;
  color: rgb(255 177 177 / var(--tw-text-opacity));
}

@media (min-width: 976px){
  .lg\:col-span-11{
    grid-column: span 11 / span 11;
  }

  .lg\:col-span-7{
    grid-column: span 7 / span 7;
  }

  .lg\:col-span-1{
    grid-column: span 1 / span 1;
  }

  .lg\:col-start-2{
    grid-column-start: 2;
  }

  .lg\:col-start-11{
    grid-column-start: 11;
  }

  .lg\:mt-0{
    margin-top: 0px;
  }

  .lg\:block{
    display: block;
  }

  .lg\:w-2\/5{
    width: 40%;
  }

  .lg\:flex-col{
    flex-direction: column;
  }

  .lg\:place-self-start{
    place-self: start;
  }

  .lg\:pt-32{
    padding-top: 8rem;
  }

  .lg\:pb-32{
    padding-bottom: 8rem;
  }

  .lg\:text-left{
    text-align: left;
  }

  .lg\:text-end{
    text-align: end;
  }
}
